.bankList {
	margin-bottom: 12px;
}

.pullFrom {
	font-weight: 500;
	padding-bottom: 4px;
}

.bankInfo {
	display: flex;
	align-items: center;
	padding: 10px 0 20px 0;
}

.removeBank {
	cursor: pointer;
	text-decoration: underline;
	font-size: 14px;
	color: var(--link-text);
	margin-left: auto;
}

.linkAccount {
	font-size: 18px;
	font-weight: 500;
	padding: 0 0 15px;
}

.transferType {
	border-radius: 4px;
	border: 1px solid var(--border-color-2);
	cursor: pointer;
	font-size: 14px;
	justify-content: space-between;
	padding: 16px;
	margin-bottom: 12px;
	background: var(--bg-white);
}